<template>
    <div class="Authentication">
        <MyMenu />
        <!-- loading -->
        <a-spin v-if="pageStatus==0" tip="Loading..." class="loading"></a-spin>

        <div v-if="pageStatus!=0">
            <a-row>
                <a-col :span="this.g.container.span" :offset="this.g.container.offset">

                    <!-- 第一步验证 -->
                    <div v-if="pageStatus==1" class="t-left" style="padding:10px;">

                        <a-form :form="form" @submit="onClickSubmit">

                            <a-row>
                                <a-col>
                                    <a-alert v-if="formParam.status==2" :message="'未通过原因：'+formParam.failed_reason" banner closable />
                                    <a-form-item label="Country">
                                        <a-input v-decorator="[
                                        'country',
                                        {
                                            rules: [{ required: true, message: 'Please input your Country!' }],
                                            initialValue:formParam.country
                                        } ]" placeholder="Country">
                                        </a-input>
                                    </a-form-item>
                                </a-col>

                                <a-col>
                                    <a-form-item label="Full Name / Id Name">
                                        <a-input v-decorator="[
                                        'full_name',
                                        {
                                            rules: [{ required: true, message: 'Please input your Full Name or Id Name!' }],
                                            initialValue:formParam.full_name
                                        } ]" placeholder="Full Name / Id Name">
                                        </a-input>
                                    </a-form-item>
                                </a-col>

                                <a-col>
                                    <a-form-item label="Id Number">
                                        <a-input v-decorator="[
                                        'id_number',
                                        {
                                            rules: [{ required: true, message: 'Please input your Id Number!' }],
                                            initialValue:formParam.id_number
                                        } ]" placeholder="Id Number">
                                        </a-input>
                                    </a-form-item>
                                </a-col>

                                <a-col>
                                    <div>
                                        <div class="ant-col ant-form-item-label"><label for="file" title="Id Number" class="ant-form-item-required">Front and back of the Id photo</label></div>
                                    </div>
                                    <a-upload-dragger id="file" name="file" :action="uploadUrl" @change="onFileUpload" :default-file-list="defaultFileList">
                                        <p class="ant-upload-drag-icon">
                                            <a-icon type="inbox" />
                                        </p>
                                        <p class="ant-upload-text">
                                            Click or drag file to this area to upload
                                        </p>
                                        <p class="ant-upload-hint">
                                            Support for a single or bulk upload. Strictly prohibit from uploading company data or other
                                            band files
                                        </p>
                                    </a-upload-dragger>
                                    <a-alert v-if="is_show_alert" message="Please upload id photo!" banner closable />
                                    <!-- <a-row :gutter="16">
                                        <a-col v-for="item in defaultFileList" :key="item.id" :span="4">
                                            <a-card style="width:64px; height:64px;">
                                                <img slot="cover" alt="example" :src="item.url" />
                                            </a-card>
                                        </a-col>
                                    </a-row> -->

                                </a-col>

                                <a-col>
                                    <a-form-item>
                                        <a-row>
                                            <a-col class="mt-60">
                                                <a-button type="primary" :loading="loading" block html-type="submit">Submit</a-button>
                                            </a-col>
                                        </a-row>
                                    </a-form-item>
                                </a-col>
                            </a-row>

                        </a-form>

                        <div class="form-group col-md-12">
                            <h4>important note ：</h4>
                            <ul>
                                <li>Please upload clear id photo</li>
                                <li>The image can be in JPG, PNG, or JPEG format and the size cannot exceed 1 MB</li>
                                <li>If you encounter problems contact customer service {{mail}}</li>
                            </ul>
                        </div>
                    </div>
                    <!-- 验证通过 或者 未通过 -->
                    <div v-if="pageStatus==2">
                        <h4>Please wait for the administrator's review！</h4>
                        <div class="mt-60"></div>
                    </div>
                    <!-- 验证通过 或者 未通过 -->
                    <div v-if="pageStatus==3" class="bg-white" style="padding:10px;">
                        <div class="mt-60"></div>
                        <h4>Identity has been authenticated!</h4>
                        <div class="mt-60"></div>
                    </div>

                </a-col>
            </a-row>
        </div>

    </div>
</template>


<script>
import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "Authentication",
    components: {
        MyMenu,
    },
    data() {
        return {
            loading: false,
            mail: "",
            user_id: this.g.userInfo.id,
            pageStatus: 0,
            uploadUrl: null,
            defaultFileList: [],
            photoList: [],
            formParam: {
                user_id: null,
                country: null,
                full_name: null,
                id_number: null,
                status: null,
                failed_reason: null,
            },
            is_show_alert: false,
        };
    },
    beforeCreate() {
        this.form = this.$form.createForm(this, { name: "auth" });
    },
    methods: {
        onClickSubmit(e) {
            e.preventDefault();
            this.form.validateFields((err, values) => {
                if (!err) {
                    // console.log("Received values of form: ", values);
                    // console.log("defaultFileList", this.defaultFileList);
                    // console.log("photoList", this.photoList);
                    values.user_id = this.user_id;
                    this.loading = true;
                    if (this.photoList.length <= 0) {
                        this.is_show_alert = true;
                        this.loading = false;
                        return false;
                    }
                    $.ajax({
                        url: `${this.g.apiUrl}/api/my/authentication`,
                        data: values,
                        type: "post",
                        error: (XMLHttpRequest, textStatus, errorThrown) => {
                            this.loading = false;
                            console.log(
                                XMLHttpRequest,
                                textStatus,
                                errorThrown
                            );
                            alert(XMLHttpRequest.responseJSON.message);
                        },
                        success: (res) => {
                            this.loading = false;
                            if (res.flag != "ok") {
                                alert(res.flag);
                                return;
                            }
                            // this.$EventBus.$emit("loginComplete", res.data);
                            // this.goMyHome();
                            // console.log(res);
                            this.initValue();
                        },
                    });
                }
            });
        },
        onFileUpload(info) {
            const status = info.file.status;
            if (status !== "uploading") {
                // console.log(info.file, info.fileList);
            }
            if (status === "done") {
                this.$message.success(
                    `${info.file.name} file uploaded successfully.`
                );
                // console.log(info);
                this.photoList.push({ name: info.file.name });
                this.is_show_alert = false;
            } else if (status === "error") {
                this.$message.error(`${info.file.name} file upload failed.`);
            }
        },
        initValue() {
            this.loading = true;
            $.get(
                `${this.g.apiUrl}/api/my/authentication`,
                {
                    user_id: this.user_id,
                },
                (res) => {
                    this.loading = false;

                    if (res.data.authInfo) {
                        this.formParam.user_id = res.data.authInfo.user_id;
                        this.formParam.country = res.data.authInfo.country;
                        this.formParam.full_name = res.data.authInfo.full_name;
                        this.formParam.id_number = res.data.authInfo.id_number;
                        this.formParam.status = res.data.authInfo.status;
                        this.formParam.failed_reason =
                            res.data.authInfo.failed_reason;
                        // '状态：0未认证,1待审核,2未通过,3通过',
                        if (this.formParam.status == 1) {
                            this.pageStatus = 2;
                        } else if (this.formParam.status == 2) {
                            this.pageStatus = 1;
                        } else if (this.formParam.status == 3) {
                            this.pageStatus = 3;
                        }
                    } else {
                        this.pageStatus = 1;
                    }
                    this.photoList = res.data.photoInfo.list;
                }
            );
        },
        init() {
            this.mail = this.g.configKey("email_admin")
            this.uploadUrl = `${this.g.apiUrl}/api/my/upload-photo?user_id=${this.user_id}`;
            this.initValue();
        },
    },
    mounted() {
        this.init();
    },
};
</script>



<style scoped>
</style>